.VirtualContainer{width:100%;margin:0px auto;padding:20px;background-color:#f9f9f9;font-family:"Segoe UI",sans-serif;display:flex;flex-direction:column;align-items:center;gap:30px}.VirtualContainer h1{font-size:2rem;font-weight:700;color:#333;margin:0;text-align:left;align-self:flex-start}.VirtualContainer h4{color:#171717;text-align:left;margin-bottom:10px;margin-left:0;align-self:flex-start}.FirstContainer1,.SecondContainer2,.ThirdContainer3{width:100%;background-color:#fff;padding:30px}.FirstContainer1:hover,.SecondContainer2:hover,.ThirdContainer3:hover{box-shadow:0 6px 20px rgba(0,188,212,.4)}.FirstContainer1{text-align:left}.FirstContainer1 h2{font-weight:bold;margin-bottom:15px;text-align:center}.FirstContainer1 h3{font-size:18px;color:#333;margin-bottom:20px}.FirstContainer1 p{font-size:16px;color:#555;text-align:left;margin:8px 0;position:relative;padding-left:35px}.FirstContainer1 p::before{content:"âœ”";position:absolute;left:0;top:50%;transform:translateY(-50%);width:25px;height:25px;background-color:green;color:#fff;font-size:14px;font-weight:bold;display:flex;align-items:center;justify-content:center;border-radius:50%}.FirstContainer1 .ContactBtn{display:flex;justify-content:center;margin-top:20px}.FirstContainer1 .ContactBtn button{background-image:linear-gradient(to right, #6a0dad, #00bcd4);color:#fff;padding:.9rem 2rem;font-size:1.1rem;font-weight:bold;border:none;border-radius:50px;cursor:pointer;box-shadow:0 4px 15px rgba(106,13,173,.3);transition:all .3s ease}.FirstContainer1 .ContactBtn button:hover{transform:scale(1.05);box-shadow:0 6px 20px rgba(0,188,212,.4)}.SecondContainer2{text-align:left}.SecondContainer2 h2{text-align:center;font-size:24px;font-weight:bold;margin-bottom:15px}.SecondContainer2 p{font-size:16px;color:#555;line-height:1.5;margin-bottom:10px}.ThirdContainer3 h2{text-align:center;font-size:24px;font-weight:bold;margin-bottom:15px}.ThirdContainer3 p{font-size:16px;color:#555;margin-bottom:10px;font-weight:500}.card{width:190px;height:254px;background:#171717;display:flex;justify-content:center;align-items:center;box-shadow:0px 0px 3px 1px rgba(0,0,0,.5333333333);cursor:pointer;position:relative;overflow:hidden;border-radius:10px;margin:10px}.card .content{border-radius:10px;background:#171717;width:186px;height:250px;z-index:1;padding:20px;color:#fff;display:flex;justify-content:center;align-items:center;position:relative;text-align:center}.card .content p{margin:0;font-size:13px;color:#fff;text-align:center;line-height:1.4;animation:fadeSlideUp .6s ease forwards;opacity:0;transform:translateY(10px)}.card .content::before{opacity:0;transition:opacity 300ms;content:" ";display:block;background:hsla(0,0%,100%,.3333333333);width:50px;height:50px;position:absolute;filter:blur(50px)}.card:hover .content::before{opacity:1}.card::before,.card::after{opacity:1;content:"";position:absolute;display:block;width:120px;height:300px;transition:opacity 300ms}.card::before{transform:rotate(0deg) translateY(50%);background:linear-gradient(90deg, #ff2288, transparent);animation:rotation_9018 3000ms infinite linear;animation-play-state:paused}.card::after{transform:rotate(0deg) translateY(-50%);background:linear-gradient(90deg, transparent, #2268ff);animation:rotation_9019 3000ms infinite linear;animation-play-state:paused}.card:hover::before,.card:hover::after{animation-play-state:running}.card:hover .glass{opacity:0}.glass{position:absolute;display:block;width:190px;height:254px;background:rgba(23,23,23,.2);backdrop-filter:blur(50px);z-index:1;transition-duration:.5s}@keyframes rotation_9018{0%{transform:rotate(0deg) translateY(50%)}100%{transform:rotate(360deg) translateY(50%)}}@keyframes rotation_9019{0%{transform:rotate(0deg) translateY(-50%)}100%{transform:rotate(360deg) translateY(-50%)}}@keyframes fadeSlideUp{to{opacity:1;transform:translateY(0)}}.ServiceCardsWrapper{display:flex;flex-wrap:wrap;gap:16px;justify-content:center;margin-top:30px}